import React, { useState, useEffect } from "react";
import { Box, Text, Image, Flex } from "@chakra-ui/react";
import audioOnIcon from "../assets/icons/audio_on.png";
import audioOffIcon from "../assets/icons/audio_off.png";

const Flashcard = ({ card, isFlipped, onFlip }) => {
  const [localCard, setLocalCard] = useState(card);
  const [isPlaying, setIsPlaying] = useState(false);
  const [audio] = useState(new Audio());
  const [volume, setVolume] = useState(0.5); // Default volume at 50%

  // Effect to handle term, definition, and pronunciation updates
  useEffect(() => {
    if (!isFlipped) {
      // Use a shorter delay to make the content update faster and update term & pronunciation together
      const timeout = setTimeout(() => {
        setLocalCard(card);
      }, 250); // A delay to give a smooth flipping experience

      return () => clearTimeout(timeout);
    }
  }, [card, isFlipped]);

  const playAudio = () => {
    const chapterNumber = card.category.match(/Chapter (\d+)/)[1];

    const audioPath = `/audio/ch${chapterNumber}_audio/${card.term
      .toLowerCase()
      .replace(/ /g, "_")}.mp3`;

    audio.src = audioPath;
    audio.volume = volume; // Set the audio volume based on slider
    audio
      .play()
      .then(() => setIsPlaying(true))
      .catch((err) => {
        console.error("Error playing audio:", err);
        setIsPlaying(false);
      });

    audio.onended = () => setIsPlaying(false);
  };

  const stopAudio = () => {
    audio.pause();
    setIsPlaying(false);
  };

  const handleAudioToggle = (event) => {
    // Prevent the card from flipping when clicking on the audio icon
    event.stopPropagation();

    if (isPlaying) {
      stopAudio();
    } else {
      playAudio();
    }
  };

  const handleFlip = () => {
    // This will flip the card when clicked anywhere except on the audio icon
    onFlip();
  };

  const renderPronunciation = (pronunciation) => {
    return pronunciation.split(/(\s|-)/).map((segment, index) => {
      if (segment.match(/[A-Z]+/)) {
        // Apply special styling to capitalized segments
        return (
          <span key={index} style={{ fontWeight: "bold", color: "#e53e3e" }}>
            {segment}
          </span>
        );
      } else {
        return <span key={index}>{segment}</span>;
      }
    });
  };

  return (
    <Box
      className={`flashcard ${isFlipped ? "flipped" : ""}`}
      sx={{
        width: "300px",
        height: "200px",
        perspective: "1000px",
        cursor: "pointer",
        margin: "20px auto",
        position: "relative",
      }}
      onClick={handleFlip}
    >
      <Box
        _before={{
          content: '""',
          position: "absolute",
          top: "0",
          left: "0",
          width: "100%",
          height: "100%",
          borderRadius: "8px",
          boxShadow: "0 8px 16px rgba(0, 0, 0, 0.15)",
          transform: "rotate(-2deg)",
          zIndex: "-1",
        }}
        _after={{
          content: '""',
          position: "absolute",
          top: "0",
          left: "0",
          width: "100%",
          height: "100%",
          borderRadius: "8px",
          boxShadow: "0 12px 24px rgba(0, 0, 0, 0.1)",
          transform: "rotate(2deg)",
          zIndex: "-2",
        }}
      />

      <Box
        className="flashcard-inner"
        sx={{
          position: "relative",
          width: "100%",
          height: "100%",
          textAlign: "center",
          transition: "transform 0.6s ease-in-out",
          transformStyle: "preserve-3d",
          transform: isFlipped ? "rotateY(180deg)" : "rotateY(0deg)",
        }}
      >
        {/* Front Side (Term and Pronunciation) */}
        <Box
          className="flashcard-front"
          sx={{
            position: "absolute",
            width: "100%",
            height: "100%",
            backfaceVisibility: "hidden",
            borderRadius: "8px",
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.3)",
            backgroundColor: "#ffffff",
            color: "#000000",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            fontWeight: "bold",
            flexDirection: "column",
            padding: "10px",
          }}
        >
          <Flex justify="center" align="center">
            {/* Audio Icon */}
            <Image
              src={isPlaying ? audioOnIcon : audioOffIcon}
              alt="Audio Icon"
              boxSize="15px"
              onClick={handleAudioToggle}
              cursor="pointer"
              mr="10px"
            />
            <Text fontSize="xl" color="teal.700">
              {localCard.term}
            </Text>
          </Flex>
          {localCard.pronunciation && (
            <Text fontSize="md" color="gray.600" mt="2">
              <em>{renderPronunciation(localCard.pronunciation)}</em>
            </Text>
          )}
        </Box>

        {/* Back Side (Definition) */}
        <Box
          className="flashcard-back"
          sx={{
            position: "absolute",
            width: "100%",
            height: "100%",
            backfaceVisibility: "hidden",
            borderRadius: "8px",
            boxShadow: "0 4px 8px rgba(0, 0, 0, 0.3)",
            backgroundColor: "#e3fcef",
            color: "#000000",
            transform: "rotateY(180deg)",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            padding: "10px",
          }}
        >
          <Text fontSize="md" color="gray.700">
            {localCard.definition}
          </Text>
        </Box>
      </Box>
    </Box>
  );
};

export default Flashcard;
