import React, { useState, useEffect, useMemo } from "react";
import {
  Box,
  Button,
  Center,
  Text,
  Checkbox,
  CheckboxGroup,
  Stack,
  Select,
  Radio,
  RadioGroup,
  Divider,
  Flex,
  SimpleGrid,
} from "@chakra-ui/react";

const QuizMode = ({ flashcardsData, categories }) => {
  const [selectedCategories, setSelectedCategories] = useState([]);
  const [numQuestions, setNumQuestions] = useState(5);
  const [cardsToShow, setCardsToShow] = useState([]);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [score, setScore] = useState(0);
  const [selectedAnswers, setSelectedAnswers] = useState({});
  const [showScore, setShowScore] = useState(false);
  const [quizStarted, setQuizStarted] = useState(false);
  const [answerChoicesList, setAnswerChoicesList] = useState([]);

  // Function to handle category selection
  const handleCategoryChange = (selected) => {
    setSelectedCategories(selected);
  };

  // Function to handle number of questions selection
  const handleNumQuestionsChange = (e) => {
    setNumQuestions(parseInt(e.target.value, 10));
  };

  // Function to start the quiz
  const handleStartQuiz = () => {
    const filteredCards =
      selectedCategories.length === 0
        ? flashcardsData
        : flashcardsData.filter((card) =>
            selectedCategories.includes(card.category),
          );
    const shuffledCards = filteredCards
      .sort(() => 0.5 - Math.random())
      .slice(0, numQuestions);
    const choices = shuffledCards.map((card) => {
      const incorrectAnswers = filteredCards
        .filter((c) => c.term !== card.term)
        .map((c) => c.term)
        .sort(() => 0.5 - Math.random())
        .slice(0, 3);

      return [card.term, ...incorrectAnswers].sort(() => 0.5 - Math.random());
    });

    setCardsToShow(shuffledCards);
    setAnswerChoicesList(choices);
    setCurrentQuestionIndex(0);
    setScore(0);
    setSelectedAnswers({});
    setShowScore(false);
    setQuizStarted(true);
  };

  // Function to handle answer selection
  const handleAnswerSelect = (value) => {
    setSelectedAnswers((prevAnswers) => ({
      ...prevAnswers,
      [currentQuestionIndex]: value,
    }));
  };

  // Function to handle submitting the quiz
  const handleSubmitQuiz = () => {
    let scoreCount = 0;
    const userAnswers = cardsToShow.map((card, index) => {
      const isCorrect = selectedAnswers[index] === card.term;
      if (isCorrect) {
        scoreCount++;
      }
      return {
        definition: card.definition,
        correctAnswer: card.term,
        selectedAnswer: selectedAnswers[index],
        isCorrect,
        answerChoices: answerChoicesList[index],
      };
    });
    setScore(scoreCount);
    setShowScore(true);
  };

  // Function to handle navigating between questions
  const handleNavigateQuestion = (index) => {
    setCurrentQuestionIndex(index);
  };

  // Function to handle restarting the quiz
  const handleRestartQuiz = () => {
    setQuizStarted(false);
    setSelectedCategories([]);
    setNumQuestions(5);
    setCardsToShow([]);
    setCurrentQuestionIndex(0);
    setScore(0);
    setSelectedAnswers({});
    setShowScore(false);
  };

  // Check if all questions are answered
  const allQuestionsAnswered =
    Object.keys(selectedAnswers).length === numQuestions;

  return (
    <Center flexDirection="column" padding={{ base: "5px", md: "20px" }}>
      {!quizStarted ? (
        <Box
          width="100%"
          maxWidth={{ base: "100%", md: "600px" }}
          padding={{ base: "15px", md: "30px" }}
          boxShadow="2xl"
          borderRadius="xl"
          textAlign="center"
          bg="white"
        >
          <Text
            fontSize={{ base: "xl", md: "2xl" }}
            fontWeight="extrabold"
            mb="8"
            color="teal.700"
          >
            Select Quiz Settings
          </Text>
          <Divider mb="6" />
          <Box mb="8">
            <Text mb="4" fontWeight="bold" fontSize="lg" color="teal.600">
              Number of Questions
            </Text>
            <Select
              value={numQuestions}
              onChange={handleNumQuestionsChange}
              maxWidth={{ base: "100%", md: "50%" }}
              bg="gray.100"
              borderRadius="md"
              boxShadow="md"
              fontSize={{ base: "md", md: "lg" }}
            >
              {[5, 10, 15, 20, 25, 30, 40, 50, 75].map((num) => (
                <option key={num} value={num}>
                  {num}
                </option>
              ))}
            </Select>
          </Box>
          <Divider mb="6" />
          <Box mb="8">
            <Text mb="4" fontWeight="bold" fontSize="lg" color="teal.600">
              Select Categories
            </Text>
            <CheckboxGroup
              value={selectedCategories}
              onChange={handleCategoryChange}
            >
              <SimpleGrid
                columns={{ base: 1, sm: 2 }}
                bg="gray.50"
                padding="4"
                borderRadius="md"
                boxShadow="md"
              >
                {categories.map((category, index) => (
                  <Flex
                    key={index}
                    alignItems="center"
                    justifyContent="flex-start"
                    padding="4"
                    borderRadius="md"
                  >
                    <Checkbox
                      value={category}
                      size={{ base: "md", md: "lg" }}
                      colorScheme="teal"
                      mr="4"
                      alignSelf="flex-start"
                    />
                    <Text textAlign="left" fontSize={{ base: "md", md: "lg" }}>
                      {category}
                    </Text>
                  </Flex>
                ))}
              </SimpleGrid>
            </CheckboxGroup>
          </Box>
          <Divider mb="6" />
          <Button
            colorScheme="teal"
            size="lg"
            onClick={handleStartQuiz}
            width="100%"
            py={{ base: "6", md: "8" }}
            boxShadow="lg"
          >
            Start Quiz
          </Button>
        </Box>
      ) : showScore ? (
        <Box
          textAlign="center"
          width="100%"
          maxWidth="800px"
          padding={{ base: "15px", md: "20px" }}
          boxShadow="lg"
          borderRadius="lg"
          bg="white"
        >
          <Text
            fontSize={{ base: "xl", md: "2xl" }}
            color="teal.700"
            fontWeight="bold"
            mb="4"
          >
            You scored {score} out of {cardsToShow.length}
          </Text>
          <Divider mb="6" />
          <Box mt="6" textAlign="left">
            {cardsToShow.map((card, index) => (
              <Box
                key={index}
                mb="4"
                padding={{ base: "10px", md: "15px" }}
                borderRadius="lg"
                boxShadow="md"
              >
                <Text fontWeight="bold" fontSize="lg" color="teal.800">
                  {index + 1}. {card.definition}
                </Text>
                <Stack spacing={2} mt="2">
                  {answerChoicesList[index].map((choice, idx) => (
                    <Box
                      key={idx}
                      padding={{ base: "8px", md: "10px" }}
                      borderRadius="md"
                      border="2px solid"
                      borderColor={
                        choice === card.term
                          ? "green.500"
                          : choice === selectedAnswers[index] &&
                              selectedAnswers[index] !== card.term
                            ? "red.500"
                            : "gray.300"
                      }
                    >
                      {String.fromCharCode(65 + idx)}. {choice}
                    </Box>
                  ))}
                </Stack>
              </Box>
            ))}
          </Box>
          <Button
            mt="6"
            colorScheme="teal"
            size="lg"
            onClick={handleRestartQuiz}
            width="100%"
          >
            Restart Quiz
          </Button>
        </Box>
      ) : (
        <>
          <Box
            width="100%"
            maxWidth={{ base: "95%", md: "600px" }}
            padding={{ base: "15px", md: "20px" }}
            boxShadow="lg"
            borderRadius="lg"
            textAlign="center"
            bg="white"
          >
            <Text
              fontSize={{ base: "xl", md: "2xl" }}
              fontWeight="bold"
              mb="4"
              color="teal.700"
            >
              {currentQuestionIndex + 1}.{" "}
              {cardsToShow[currentQuestionIndex].definition}
            </Text>
            <RadioGroup
              onChange={handleAnswerSelect}
              value={selectedAnswers[currentQuestionIndex] || ""}
            >
              <Stack spacing={4} direction="column" alignItems="start">
                {answerChoicesList[currentQuestionIndex].map(
                  (choice, index) => (
                    <Radio
                      key={index}
                      value={choice}
                      colorScheme="teal"
                      size={{ base: "md", md: "lg" }}
                    >
                      {String.fromCharCode(65 + index)}. {choice}
                    </Radio>
                  ),
                )}
              </Stack>
            </RadioGroup>
            <Flex mt="8" justifyContent="space-between" wrap="wrap">
              <Button
                colorScheme="teal"
                size={{ base: "sm", md: "md" }}
                onClick={() => handleNavigateQuestion(currentQuestionIndex - 1)}
                isDisabled={currentQuestionIndex === 0}
              >
                Previous
              </Button>
              <Button
                colorScheme="teal"
                size={{ base: "sm", md: "md" }}
                onClick={() => handleNavigateQuestion(currentQuestionIndex + 1)}
                isDisabled={currentQuestionIndex === cardsToShow.length - 1}
              >
                Next
              </Button>
            </Flex>
          </Box>
          <Flex wrap="wrap" justifyContent="center" mt="4">
            {cardsToShow.map((_, index) => (
              <Button
                key={index}
                size={{ base: "xs", md: "sm" }}
                m="2"
                onClick={() => handleNavigateQuestion(index)}
                variant="outline"
                colorScheme="teal"
                opacity={selectedAnswers[index] ? 1 : 0.5}
              >
                {index + 1}
              </Button>
            ))}
          </Flex>
          <Button
            mt="6"
            colorScheme="teal"
            size="lg"
            onClick={handleSubmitQuiz}
            isDisabled={!allQuestionsAnswered}
            width="100%"
          >
            Submit Quiz
          </Button>
        </>
      )}
    </Center>
  );
};

export default QuizMode;
