import React, { useState, useEffect } from "react";
import { Center, Button, Text, Box } from "@chakra-ui/react";
import Flashcard from "./Flashcard";
import CategorySelect from "./CategorySelect";

const FlashcardMode = ({ flashcardsData, categories }) => {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [selectedCategory, setSelectedCategory] = useState("All");
  const [cardsToShow, setCardsToShow] = useState(flashcardsData);
  const [isFlipped, setIsFlipped] = useState(false);

  // Function to shuffle the flashcards
  const shuffleFlashcards = () => {
    const shuffledCards = [...cardsToShow].sort(() => Math.random() - 0.5);
    setCardsToShow(shuffledCards);
    setCurrentIndex(0);
    setIsFlipped(false);
  };

  const handleCategoryChange = (e) => {
    const category = e.target.value;
    setSelectedCategory(category);
    const filteredCards =
      category === "All"
        ? flashcardsData
        : flashcardsData.filter((card) => card.category === category);
    setCardsToShow(filteredCards);
    setCurrentIndex(0);
    setIsFlipped(false);
  };

  const nextCard = () => {
    if (isFlipped) {
      // If we're on the definition side, flip back first and then change the card
      setIsFlipped(false);
      setTimeout(() => {
        setCurrentIndex((prevIndex) =>
          prevIndex + 1 < cardsToShow.length ? prevIndex + 1 : prevIndex,
        );
      }, 50); // Short delay for the flip effect
    } else {
      // If we're on the term side, change the card instantly
      setCurrentIndex((prevIndex) =>
        prevIndex + 1 < cardsToShow.length ? prevIndex + 1 : prevIndex,
      );
    }
  };

  const prevCard = () => {
    if (isFlipped) {
      // If we're on the definition side, flip back first and then change the card
      setIsFlipped(false);
      setTimeout(() => {
        setCurrentIndex((prevIndex) =>
          prevIndex - 1 >= 0 ? prevIndex - 1 : prevIndex,
        );
      }, 50); // Short delay for the flip effect
    } else {
      // If we're on the term side, change the card instantly
      setCurrentIndex((prevIndex) =>
        prevIndex - 1 >= 0 ? prevIndex - 1 : prevIndex,
      );
    }
  };

  const handleCardFlip = () => {
    setIsFlipped((prevState) => !prevState);
  };

  // Update the flashcards when the selected category changes
  useEffect(() => {
    handleCategoryChange({ target: { value: selectedCategory } });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedCategory]);

  return (
    <Center flexDirection="column" padding="20px">
      {/* Category Selection and Shuffle Button */}
      <Box mb="20px" width="100%" maxWidth="600px" textAlign="center">
        <Box maxWidth="300px" width="100%" mx="auto">
          <CategorySelect
            categories={["All", ...categories]}
            selectedCategory={selectedCategory}
            handleCategoryChange={handleCategoryChange}
          />
        </Box>
        <Button mt="50px" colorScheme="teal" onClick={shuffleFlashcards}>
          Shuffle Flashcards
        </Button>
      </Box>

      {/* Flashcard */}
      {cardsToShow.length > 0 ? (
        <Flashcard
          card={cardsToShow[currentIndex]}
          isFlipped={isFlipped}
          onFlip={handleCardFlip}
        />
      ) : (
        <Text fontSize="xl" mt="20px">
          No flashcards available.
        </Text>
      )}

      {/* Navigation Buttons and Counter */}
      {cardsToShow.length > 0 && (
        <Center mt="20px" alignItems="center">
          <Button
            onClick={prevCard}
            mr="60px"
            colorScheme="teal"
            isDisabled={currentIndex === 0}
          >
            Previous
          </Button>
          <Text fontSize="lg">
            {currentIndex + 1}/{cardsToShow.length}
          </Text>
          <Button
            onClick={nextCard}
            ml="60px"
            colorScheme="teal"
            isDisabled={currentIndex === cardsToShow.length - 1}
          >
            Next
          </Button>
        </Center>
      )}
    </Center>
  );
};

export default FlashcardMode;
