import React, { useState } from "react";
import { Box, Text, Image, Flex } from "@chakra-ui/react";
import audioOnIcon from "../assets/icons/audio_on.png";
import audioOffIcon from "../assets/icons/audio_off.png";

const ReviewCard = ({ card }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [audio] = useState(new Audio());

  const playAudio = () => {
    const chapterNumber = card.category.match(/Chapter (\d+)/)[1];

    const audioPath = `/audio/ch${chapterNumber}_audio/${card.term
      .toLowerCase()
      .replace(/ /g, "_")}.mp3`;

    audio.src = audioPath;
    audio
      .play()
      .then(() => setIsPlaying(true))
      .catch((err) => {
        console.error("Error playing audio:", err);
        setIsPlaying(false);
      });

    audio.onended = () => setIsPlaying(false);
  };

  const stopAudio = () => {
    audio.pause();
    setIsPlaying(false);
  };

  const handleAudioToggle = () => {
    if (isPlaying) {
      stopAudio();
    } else {
      playAudio();
    }
  };

  // Function to style capitalized parts of the pronunciation
  const renderPronunciation = (pronunciation) => {
    return pronunciation.split(/(\s|-)/).map((segment, index) => {
      if (segment.match(/[A-Z]+/)) {
        // Apply special styling to capitalized segments
        return (
          <span key={index} style={{ fontWeight: "bold", color: "#e53e3e" }}>
            {segment}
          </span>
        );
      } else {
        return <span key={index}>{segment}</span>;
      }
    });
  };

  return (
    <Box
      width="300px"
      margin="20px auto"
      padding="20px"
      borderRadius="8px"
      boxShadow="0 4px 8px rgba(0, 0, 0, 0.3)"
      backgroundColor="#ffffff"
      color="#000000"
      textAlign="center"
      position="relative"
    >
      <Flex justify="center" align="center">
        {/* Audio Icon */}
        <Image
          src={isPlaying ? audioOnIcon : audioOffIcon}
          alt="Audio Icon"
          boxSize="15px"
          onClick={handleAudioToggle}
          cursor="pointer"
          mr="10px"
        />
        <Text fontSize="xl" fontWeight="bold" color="teal.700">
          {card.term}
        </Text>
      </Flex>
      {card.pronunciation && (
        <Text fontSize="md" color="gray.600" mt="2">
          <em>{renderPronunciation(card.pronunciation)}</em>
        </Text>
      )}
      <Text fontSize="md" color="gray.700" mt="4">
        {card.definition}
      </Text>
    </Box>
  );
};

export default ReviewCard;
