// src/components/ReviewMode.js

import React, { useState, useEffect } from "react";
import { Center, Flex, Button, Text, Box } from "@chakra-ui/react";
import CategorySelect from "./CategorySelect";
import ReviewCard from "./ReviewCard";

const ReviewMode = ({ flashcardsData, categories }) => {
  const [selectedCategory, setSelectedCategory] = useState("All");
  const [cardsToShow, setCardsToShow] = useState(flashcardsData);
  const [currentLetter, setCurrentLetter] = useState(null);

  // Function to handle category changes
  const handleCategoryChange = (e) => {
    const category = e.target.value;
    setSelectedCategory(category);
    const filteredCards =
      category === "All"
        ? flashcardsData
        : flashcardsData.filter((card) => card.category === category);
    setCardsToShow(filteredCards);
    setCurrentLetter(null); // Reset current letter when category changes
  };

  // Function to handle letter button clicks
  const handleLetterClick = (letter) => {
    setCurrentLetter(letter);

    // Scroll to the top after filtering
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  // Filter cards based on the selected letter
  const filteredCards = currentLetter
    ? cardsToShow.filter(
        (card) => card.term.charAt(0).toUpperCase() === currentLetter,
      )
    : cardsToShow;

  const alphabet = "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split("");

  // Determine which letters have terms starting with them
  const lettersWithTerms = new Set(
    cardsToShow.map((card) => card.term.charAt(0).toUpperCase()),
  );

  return (
    <Center flexDirection="column" padding={{ base: "10px", md: "20px" }}>
      {/* Category Selection */}
      <Box
        mb={{ base: "10px", md: "20px" }}
        width="100%"
        maxWidth="600px"
        textAlign="center"
      >
        <CategorySelect
          categories={["All", ...categories]}
          selectedCategory={selectedCategory}
          handleCategoryChange={handleCategoryChange}
        />
      </Box>

      {/* A-Z Navigation */}
      <Flex
        wrap="wrap"
        justifyContent="center"
        mb={{ base: "10px", md: "20px" }}
        paddingX={{ base: "10px", md: "0" }}
      >
        {alphabet.map((letter) => (
          <Button
            key={letter}
            size={{ base: "xs", md: "sm" }}
            m="4px"
            onClick={() => handleLetterClick(letter)}
            variant={currentLetter === letter ? "solid" : "outline"}
            colorScheme="teal"
            opacity={lettersWithTerms.has(letter) ? 1 : 0.3}
            isDisabled={!lettersWithTerms.has(letter)}
          >
            {letter}
          </Button>
        ))}
        <Button
          size={{ base: "xs", md: "sm" }}
          m="4px"
          onClick={() => {
            setCurrentLetter(null);
            window.scrollTo({ top: 0, behavior: "smooth" });
          }}
          variant={!currentLetter ? "solid" : "outline"}
          colorScheme="teal"
        >
          All
        </Button>
      </Flex>

      {/* Displaying Review Cards */}
      {filteredCards.length > 0 ? (
        filteredCards.map((card, index) => (
          <ReviewCard key={index} card={card} />
        ))
      ) : (
        <Text fontSize="xl" mt="20px">
          No terms available.
        </Text>
      )}

      {/* Back to the Top Button */}
      <Button
        position="fixed"
        bottom={{ base: "20px", md: "30px" }}
        right={{ base: "20px", md: "30px" }}
        colorScheme="teal"
        size={{ base: "sm", md: "md" }}
        onClick={() => window.scrollTo({ top: 0, behavior: "smooth" })}
        boxShadow="md"
        padding={{ base: "10px", md: "12px" }}
      >
        Back to the top
      </Button>
    </Center>
  );
};

export default ReviewMode;
